import { MainService } from './../../../services/main.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-hard-skils',
  templateUrl: './hard-skils.component.html',
  styleUrls: ['./hard-skils.component.scss']
})
export class HardSkilsComponent implements OnInit {

  languageOnSite: string = 'eng';

  listOfSkils = [
    ['HTML5 & CSS3', 5],
    ['SASS & SCSS', 4],
    ['JavaScript', 4],
    ['Bootstrap 4', 4],
    ['Gulp 4', 4],
    ['Git', 3],
    ['Node.js', 2],
    ['TypeScript', 5],
    ['Angular', 4],
    ['Figma', 2],
    ['Webpack', 2]
  ]

  skils = {
    ru: this.listOfSkils,
    eng: this.listOfSkils
  }

  constructor(private mainService: MainService) { }

  ngOnInit() {
    this.getlanguageOnSite();
  }

  getlanguageOnSite() {
    this.mainService.languageOnSite.subscribe(data => this.languageOnSite = data);
  }

  getQuantity(num: number, state: string): number[] {
    const arrCheked = new Array(num).fill(1);
    const arrUnChecked = new Array(6).fill(1);
    if (state === 'checked') return arrCheked;
    if (state === 'unChecked') return arrUnChecked.splice(0, arrUnChecked.length - num);
  }
}
